@import url(https://fonts.googleapis.com/css2?family=Prompt&display=swap);
*{border:0;margin:0;padding:0}body{font-family:"Prompt", Arial, sans-serif;width:100vw;height:100vh;background:#333333;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

@font-face{font-family:"F2";src:url(/kart-optimizer/static/media/mario_kart_f2.0651abbb.ttf) format("truetype")}.Header{grid-area:header;width:100%;height:100%;padding:1rem;box-sizing:border-box;background:linear-gradient(#26baff, #f92470);box-shadow:0 5px 10px rgba(0,0,0,0.25);line-height:4rem;word-spacing:-10px}.Logo{height:100%;width:45rem;margin:auto}.LogoImg{float:left;height:100%;margin-right:1rem}.LogoText{color:white;font-size:28px;font-family:"F2";-webkit-text-stroke-width:2px;-webkit-text-stroke-color:#333333}

.KartOptimizerApp{display:grid;grid-template-areas:"header header" "kart-config kart-stats" "kart-config kart-stats";grid-template-rows:6rem calc(50vh - 3rem) calc(50vh - 3rem);grid-template-columns:50vw 50vw}

.KartConfig{grid-area:kart-config;display:flex;justify-content:center;align-items:center;box-sizing:border-box;width:100%;height:100%;padding:2rem;background:#222}.CenterContainer{width:40rem;height:44.25rem}.PartOptions{display:flex}.Separator{width:calc(100% - 1rem);height:0.25rem;background:linear-gradient(to right, #26baff, #f92470);border-radius:1rem;margin:1rem 0.5rem}

.PartSelection{box-sizing:border-box;width:9em;height:11rem;margin:0.5rem}.PartLabel{color:white;box-sizing:border-box;font-size:18px;text-align:center;margin-top:0.75rem}.SelectedPart{display:flex;align-items:center;justify-content:center;box-sizing:border-box;width:9rem;height:9rem;padding:0.5rem;border-radius:2rem;transition:0.2s}.SelectedPart:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}

.PartTile{position:relative;display:flex;justify-content:center;box-sizing:border-box;align-items:center;box-sizing:border-box;width:4rem;height:4rem;margin:0.5rem;padding:0.25rem;border-radius:1rem;transition:0.2s}.PartTile:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.PartImage{width:100%}

.PartList{display:flex;flex-wrap:wrap;justify-content:center}

.ProgressBar{width:100%;color:white;font-size:20px;margin-bottom:1rem}.ProgressBar p{margin-bottom:0.25rem}.Bar{position:relative;width:100%;background:#777;border-radius:1rem;height:1rem;background:linear-gradient(to right, #26baff, #f92470);overflow:hidden}.SeparatorContainer{position:absolute;display:flex;justify-content:space-evenly;align-items:center;width:100%;height:1rem;top:0}.Progress{position:absolute;right:0;top:0;height:1rem;background:gray}.VerticalSeparator{height:100%;width:2px;background:#333}

.PartStats{display:flex;justify-content:center;align-items:center;grid-area:kart-stats;box-sizing:border-box;width:100%;height:100%;background:#444;padding:0 6rem}.StatsContainer{height:20rem;width:100%}

